/*=== SMALL DEVICES ===*/

@media screen and (max-width: 360px) {
    .svg__img {
        width: 100px;
    }

    .section {
        padding: 3.5rem 0 1rem;
    }

    .services__img {
        width: 100px;
    }

    .app__buttons {
        grid-template-columns: max-content;
    }
}

/*=== MEDIUM DEVICES ===*/

@media screen and (min-width: 576px) {
    .svg__img {
        width: 100%;
    }

    .section__title-center {
        text-align: initial;
    }

    .home__container,
    .about__container,
    .security__container,
    .services__container,
    .app__container,
    .contact__container,
    .footer__container{
        grid-template-columns: repeat(2, 1fr);
    }
    
    .home__img {
        order: 1;
    }

    .home__container {
        align-items: center;
    }

    .about__data,
    .security__data,
    .app__data,
    .contact__description{
        text-align: initial;
    }

    .about__img,
    .app__img{
        order: -1;
    }
} 

@media screen and (min-width: 767px) {
    body {
        margin: 0;
    }

    .section {
        padding: 6rem 0 2rem;
    }

    .nav {
        height: calc(var(--header-height) + 1.5rem);

        &__list {
            flex-direction: row;
            column-gap: 2.5rem;
        }
        &__toggle {
            display: none;
        }
    }

    .change-theme {
        position: initial;
    }

    .home__container  {
        padding: 6rem 0 2rem;
    }

    .app__buttons {
        justify-content: initial;
    }
}

/* FOR LARGE DEVICES */
@media screen and (min-width: 968px) {
    .button__header {
        display: block;
    }

    .svg__img {
        width: 470px;
    }

    .about__container,
    .security__container,
    .app__container,
    .contact__container{
        column-gap: 6rem;
    }

    .services__container {
        grid-template-columns: repeat(3, 1fr);
    }

    .contact__container {
        grid-template-columns: 3fr 1.5fr 1.25fr;
    }

    .footer {
        &__container {
            grid-template-columns: repeat(5, 1fr);
        }
        &__social {
            align-items: flex-start;

            &-link {
                font-size: 1.45rem;
            }
        }
    }
}

@media screen and (min-width: 1024px) {
    .container {
        margin-left: auto;
        margin-right: auto;
    }

    .home {
        &__img {
            width: 540px;
        }

        &__description {
            padding-right: 5rem;
        }
    }

    .footer__container {
        column-gap: 3rem;
    }

    .scrollup {
        right: 2rem;
    }
}